import React from 'react'
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from 'react-responsive-carousel'

const projects= [
	{url:'https://mouawiashride.github.io/pizza_app/',skillOfProj:'React with FramerMotion',alt:'React_with_framerMotion',projName:'PizzaApp',projCover:['/assets/images/slider/pizzaApp.png','/assets/images/slider/pizzaApp.webp']},
	{url:'https://mouawiashride.github.io/shop4/',skillOfProj:'React , material ui ,commerce.js ,stripe ',alt:'React_material_ui_commerce.js_stripe',projName:'shop4',projCover:['/assets/images/slider/shop4.png','/assets/images/slider/shop4.webp']},
	{url:'https://mouawiashride.github.io/shop4_flux/',skillOfProj:'React ,flux , material ui ,commerce.js ,stripe ',alt:'React_material_ui_commerce.js_stripe',projName:'shop4',projCover:['/assets/images/slider/shop4.png','/assets/images/slider/shop4.webp']},
	{url:'https://mouawiashride.github.io/',skillOfProj:'React , material ui , recharts  ',alt:'React_material_ui',projName:'dashBoard',projCover:['/assets/images/slider/dashBoard.png','/assets/images/slider/dashBoard.webp']},

]
export default function Slider() {

	const slider = projects.map((proj,index)=>{
		return (
		<div key={index} className='h-[100%]'>
            <a className='hover:cursor-pointer block' target={'_blank'} href={''+proj.url} rel="noreferrer" >
										<picture className='h-[inherit]' >
						<source srcset={proj.projCover[1]} type="image/webp" />
						<source srcset={proj.projCover[0]} type="image/png" />
						<img  alt={proj.alt} src={proj.projCover[0]} type="image/vnd.ms-photo" />
											</picture>
				  </a>
			 <p className="  select-none legend">
     {proj.skillOfProj}
			 </p>
		</div>
		)
	})
  return (
<>
    
  
  
          <Carousel className=' justify-center items-center  rounded-[20px] overflow-hidden mt-8  mx-auto w-auto  max-w-[1200px]  ' showArrows={false}  showStatus={false}  showThumbs={false} interval={2000} autoPlay={true} infiniteLoop={true}>

                {slider}
            </Carousel>


 
  

    </>

  )
}
